<template>
    <v-container>
        <v-form ref="form" v-model="valid">
        <v-row>
            <v-col cols="12" sm="6">
                <v-autocomplete
                    label="Selected Affiliate"
                    v-model="selectedAffiliateId"
                    v-model:search="affiliatesSearchText"
                    :items="affiliates"
                    :customFilter="searchAffiliates"
                    :loading="affiliatesSearchLoading"
                    item-title="AffiliateName"
                    item-value="AffiliateId"
                    variant="underlined"
                    placeholder="Select Affiliate"
                    persistent-hint
                    hide-no-data
                    :rules="[rules.required]"
                    v-on:update:model-value="selectAffiliate"
                    v-on:click="loadDefaultAffiliates"
                >
                    <template v-slot:[`item`]="{ item }">
                        {{ getAffiliateLabel(item) }}
                    </template>

                    <template v-slot:progress>
                        <v-progress-linear
                            indeterminate
                            height="5"
                            color="#3f98c9"
                            absolute
                        ></v-progress-linear>
                    </template>
                </v-autocomplete>
            </v-col>
        </v-row>
        <v-row>
            <v-col cols="12" sm="6">
                <v-select
                    :items="typesList"
                    :multiple="isMultiple"
                    label="Collection Type"
                    item-value="value"
                    item-title="label"
                    v-model="memberFormType"
                    variant="underlined"
                    :rules="[rules.required]"
                    :item-props="{ disabled: isOptionDisabled }"
                ></v-select>
            </v-col>
        </v-row>
        <v-dialog
            v-model="dialog"
            persistent
            max-width="290"
        >
            <v-card>
                <v-card-title />
                <v-card-text>as you have selected "All templates", other options are disabled</v-card-text>
                <v-card-actions>
                    <v-btn
                        color="green-darken-1"
                        variant="text"
                        @click="dialog = false"
                    >
                        Dismiss
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog
            v-model="dialog1"
            persistent
            max-width="290"
        >
            <v-card>
                <v-card-title />
                <v-card-text>This affiliate has already been taken, please choose a different one</v-card-text>
                <v-card-actions>
                    <v-btn
                        color="green-darken-1"
                        variant="text"
                        @click="dialog1 = false"
                    >
                        Dismiss
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-row class="mt-8">
            <v-col cols="12" class="d-flex ga-4">
                <v-btn size="large" color="red" :to="{ name: 'memberFormsList' }">Cancel</v-btn>
                <v-btn size="large" color="green" @click="saveMemberFormAssign">Save Changes</v-btn>
            </v-col>
        </v-row>
        </v-form>
    </v-container>
</template>

<script>
    import { debounce } from "lodash-es";
    export default {
        name: 'MemberFormCreateComponent',
        data: () => ({
            affiliates: [],
            affiliatesSearchText: null,
            affiliatesSearchLoading: false,
            selectedAffiliateId: null,
            editor: null,
            valid: true,
            rules: {
                required: value => !!value || 'Required.'
            },
            typesList: [
                { 'value': '100', 'label': 'ALL templates' },
                // { 'value': '3', 'label': 'Payroll Deduction' },
                { 'value': '4', 'label': 'Payroll Deduction and COPE' },
                { 'value': '5', 'label': 'Payroll COPE' },
                // { 'value': '1', 'label': 'eDues' },
                { 'value': '2', 'label': 'eDues and COPE' },
                { 'value': '6', 'label': 'Recurring COPE' },
                { 'value': '8', 'label': 'State Fed Recurring COPE' },
            ],
            memberFormType:null,
            isMultiple: true,
            dialog: false,
            dialog1: false
        }),
        created() {
            this.debounceInput = debounce(function (search) {
                if (this.selectedAffiliate && search === this.selectedAffiliate.AffiliateName) {
                    return;
                }
                this.affiliatesSearchLoading = true;
                axios.post('/api/v2/search/affiliate?scope=global', {
                    search: search
                })
                    .then(response => {
                        this.affiliates = response.data.data;
                        if (response.data.meta.last_page > 1) {
                            this.affiliates.push({AffiliateName:'<strong style="color:#092a5c;">Click to see all affiliates here >></strong>', AffiliateNumber:'', AffiliateId: -1});
                        }
                        this.affiliatesSearchLoading = false;
                    });
            }, 500);
        },
        methods: {
            isOptionDisabled(item) {
                return this.memberFormType && this.memberFormType.includes('100') && item.value !== '100';
            },
            saveMemberFormAssign() {
                if (!this.$refs.form.validate()) {
                    window.scrollTo({top: 0, behavior: 'smooth'});
                    // console.log('not valid');
                    return;
                }
                axios.post('/admin/api/member-form-assign', {
                    affiliate_id: this.selectedAffiliateId,
                    type: this.memberFormType
                })
                    .then(response => {
                        this.$router.replace({ name: 'memberFormsList' })
                    })
                    .catch(error => {
                        this.errors = error.response.data.errors;
                        console.log('error', this.errors);
                        if(this.errors.affiliate_id){
                            this.dialog1 = true;
                        }
                    })
                    .finally(() => {

                    });

            },
            searchAffiliates(item, queryText, itemText) {
                const searchText = queryText.toLowerCase();
                return item.AffiliateId < 0 || item.AffiliateName.toLowerCase().indexOf(searchText) > -1 || item.AffiliateNumber.toLowerCase().indexOf(searchText) > -1;
            },
            selectAffiliate(affiliateId) {
                if (affiliateId>0) {

                } else {

                }
            },
            getAffiliateLabel(affiliate) {
                let label =  affiliate.AffiliateName;
                if (affiliate.AffiliateId > 0) {
                    label += ' (' + affiliate.AffiliateNumber + ')'
                }
                return label;
            },
            loadDefaultAffiliates() {
                if (this.affiliates.length === 1 && this.affiliates.pop().AffiliateName === this.selectedAffiliate.AffiliateName) {
                    axios.post('/api/v2/search/affiliate?scope=global', {
                        search: ''
                    })
                        .then(response => {
                            if (this.affiliates.indexOf((affiliate) => {return affiliate.AffiliateName === this.selectedAffiliate.AffiliateName}) !== -1) {
                                this.affiliates = [];
                            }
                            this.affiliates = this.affiliates.concat(response.data.data);
                            if (response.data.meta.last_page > 1) {
                                this.affiliates.push({AffiliateName:'<strong style="color:#092a5c;">Click to see all affiliates here >></strong>', AffiliateNumber:'', AffiliateId: -1});
                            }
                        });
                }
            },
        },
        watch: {
            affiliatesSearchText(text) {
                this.debounceInput(text);
            },
            memberFormType: function ( value ) {
                const selectedTypes = value;
                if(selectedTypes && selectedTypes.length>1 && selectedTypes.includes('9')){
                    this.dialog = true;
                    this.memberFormType = ['9'];
                }
            },
        }
    }
</script>

<style>
    .textFieldHeader input { font-size: 1.1rem; }
</style>
